import Yox from 'yox'

import template from './template/Dialog.hbs'

import {
  TRUE,
  FALSE,
  RAW_STRING,
  RAW_BOOLEAN,
  DOCUMENT,
} from '../constant'

import {
  onTransitionEnd,
} from '../util'

const CLASS_OPEN = 'bell-dialog-open'
const CLASS_LEAVE = 'bell-dialog-leave'

export default Yox.define({
  propTypes: {
    title: {
      type: RAW_STRING,
      value: '',
    },
    open: {
      type: RAW_BOOLEAN,
    },
    closable: {
      type: RAW_BOOLEAN,
      value: TRUE,
    },
    className: {
      type: RAW_STRING,
    },
    style: {
      type: RAW_STRING,
    }
  },

  template,

  model: 'open',

  watchers: {
    open(isOpen) {
      const element = this.$el
      if (isOpen) {
        Yox.dom.addClass(element, CLASS_OPEN)
      }
      else {
        Yox.dom.addClass(element, CLASS_LEAVE)
        onTransitionEnd(
          element,
          function () {
            Yox.dom.removeClass(element, CLASS_OPEN)
            Yox.dom.removeClass(element, CLASS_LEAVE)
          }
        )
      }
    }
  },

  methods: {
    close() {
      this.set('open', FALSE)
    }
  },

  afterMount() {
    Yox.dom.append(DOCUMENT.body, this.$el)
  },

  beforeDestroy() {
    Yox.dom.remove(DOCUMENT.body, this.$el)
  }
})
