import { add, remove, update } from './base'
import Yox from 'yox';
import { NULL } from '../constant';

interface Config {
  type?: string,
  color?: string,
  height?: number,
  percent?: number
}

let config: Config = {}

function updateConfig(data: Config) {
  config.type = data.type ? data.type : config.type
  config.color = data.color ? data.color : config.color
  config.height = data.height ? data.height : config.height
}

export default {
  //  0 
  start(options: Config = {}) {
    return add(
      Yox.object.extend(
        options,
        config
      )
    )
  },
  // 
  finish() {
    update({
      percent: 100
    })
    setTimeout(
      function () {
        return remove()
      },
      1000
    )
  },
  // 
  update(data: Config) {
    return update(data)
  },
  config(data: Config) {
    updateConfig(data)
  },
  destroy() {
    config = NULL
  }
}